      await this.init();
    } catch (error) {
      if (error.message == NETWORK_ERROR_STATUS) {
        this.$toast({
          message: "网络异常 请稍后重试",
        });
      } else if (error.message != CANEAL_TOCKEN_STATUS) {
        this.$toast({
          message: error.message,
        });
      }

      return;
    }

    // 每30s发一次心跳检测，告知后端该用户仍活跃，默认执行一次
    this.sessionHeartbeat();
    setInterval(() => {
      this.sessionHeartbeat();
    }, 30 * 1000);

    // 监听页面关闭，退出消息会话
    window.addEventListener("onbeforeunload", () => {
      this.messageQuit();
    });

    // 2.滚动到底部
    this.goEnd(() => {
      this.pageLoading = false;
    });
    // 3.设置第一条消息id
    this.setFirstMsgId();
    // 4.判断消息是否需要查看更多按钮
    this.initMoreBtn();
    // 5.监听输入框高度，同步变更home的下padding
    const elementResizeDetectorMaker = require("element-resize-detector");
    const erd = elementResizeDetectorMaker();
    erd.listenTo(
      document.getElementsByClassName("chat-footer"),
      function (element) {
        var height = element.offsetHeight;
        document.getElementsByClassName("home")[0].style["padding-bottom"] =
          height + 0 + "px";
      }
    );
  },
  methods: {
    // 初始化
    async init() {
      const params = getQueryValue(
        "https://www.baidu.com/?uid=admin&rid=1&fid=2&channel=小明"
      );
      if (!params.channel) {
        throw new Error("参数错误");
      }

      this.channelId = params.channel;

      try {
        await this.initChatInfo();
        await this.initUserProfile();
        await this.initBanner();
        for await (const rideItem of this.rideTabList.tabList) {
          await this.initRideList({
            page: rideItem.page,
            size: rideItem.size,
            pay_method: rideItem.value,
          });
        }
        if ([WAIT_STATUS_ENUM.fail].includes(this.chatInfo.state)) {
          return;
        }

        // 查历史记录
        let messageList = await this.getMessage({
          mode: PULL_MESSAGE_TYPE_ENUM.prev,
          start_message_id: "",
        });
        messageList = this.filterMessage(messageList);
        console.log("=======历史记录tmessageList", messageList);
        this.chatList.unshift(...messageList);
        this.addChatTime(this.chatList);
        await this.initHotIssueList();

        this.goEnd();
        // 开始长轮询查会话返回
        // this.getMessageRegularTime();
      } catch (error) {
        throw new Error(error.message);
      }
    },
    // 初始化登录
    async initLogin() {
      console.log("initLogin invoke");

      let resp;
      try {
        resp = await login();
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }

      lostorage.setItem({
        name: ACCESS_TOKEN,
        value: resp.token,
        expires: resp.token_expire_unix * 1000,
      });
      lostorage.setItem({
        name: REFRESH_TOKEN,
        value: resp.token_refresh_unix,
        expires: resp.token_refresh_unix * 1000,
      });
    },
    // 初始化用户信息
    async initUserProfile() {
      console.log("initUserProfile invoke");

      let resp;
      try {
        resp = await getUserProfile();
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }

      this.userProfile = resp;
    },
    // 初始化聊天窗消息
    async initChatInfo() {
      console.log("initChatInfo invoke");

      let resp;
      try {
        resp = await this.initMessageInfo();
      } catch (error) {
        if (error.message != CANEAL_TOCKEN_STATUS) {
          throw new Error(error.message);
        }
        return;
      }
      switch (this.chatInfo.state) {
        case WAIT_STATUS_ENUM.init:
          // 发送欢迎语
          this.chatList.push({
            id: getUId(),
            createUnix: parseInt(new Date().getTime() / 1000),
            sendSuss: true,
            sendLoading: false,
            showMoreBtn: false,
            moreId: getUId(),
            customId: getUId(),
            role: CHAT_ROLE_TYPE_ENUM.robot,
            contentType: CHAT_CONTENT_TYPE_ENUM.auto,
            kfname: this.chatInfo.chat_window.robot.nickname,
            kfAvatar: this.chatInfo.chat_window.robot.avatar_url,
            content: {
              text: this.chatInfo.chat_window.welcome_message,
            },
          });
          break;
        case WAIT_STATUS_ENUM.inqueue:
          // eslint-disable-next-line no-case-declarations
          let switchSuss = false;
          // 默认执行一次
          try {
            const resp = await this.getQueueStatus();
            console.log("==========getQueueStatus", resp);

            if (
              resp.state == WAIT_STATUS_ENUM.success ||
              resp.in_queue_count == 0
            ) {
              // 接入成功
              this.chatList.push({
                role: CHAT_ROLE_TYPE_ENUM.hangUpCard,
                content: "",
                customId: getUId(),
                kfId: resp.staff_info.user_id,
                kfname: resp.staff_info.nickname,
                session_id: resp.session_id,
              });
              this.chatList.push({
                role: CHAT_ROLE_TYPE_ENUM.staff,
                contentType: CHAT_CONTENT_TYPE_ENUM.text,
                content: {
                  text: `您好，客服${resp.staff_info.nickname}为您服务，请问您有什么需要咨询的问题吗？`,
                },
                customId: getUId(),
                sendSuss: true,
                sendLoading: false,
                showMoreBtn: false,
                moreId: getUId(),
                kfId: resp.staff_info.user_id,
                kfname: resp.staff_info.nickname,
                kfAvatar: resp.staff_info.avatar_url,
              });
              this.chatInfo.session_id = resp.session_id;
              switchSuss = true;
              this.watchChatActive();
            } else if (resp.state == WAIT_STATUS_ENUM.inqueue) {
              // 排队中
              if (this.waitInfo.in_queue_count === resp.in_queue_count) {
                //排队人数不变，不重新发送
                return;
              }
              this.chatList.push({
                role: CHAT_ROLE_TYPE_ENUM.roundTip,
                customId: getUId(),
                content: `在当前排队${resp.in_queue_count}人，请稍后…`,
              });
            } else if (resp.state == WAIT_STATUS_ENUM.fail) {
              // 转留言
              this.chatList.push({
                role: CHAT_ROLE_TYPE_ENUM.leaveMsgBtn,
                customId: getUId(),
                content:
                  "很抱歉，暂时无法为您提供服务，请留言我们会尽快与您取得联系！",
              });
              switchSuss = true;
            }
            this.goEnd(null, 100);
            this.waitInfo = resp;
          } catch (error) {
            if (error.message == NETWORK_ERROR_STATUS) {
              this.chatList.push({
                role: CHAT_ROLE_TYPE_ENUM.defaultTip,
                customId: getUId(),
                content: "- 网络异常 请稍后重试 -",
              });
              this.goEnd(null, 100);
            } else if (error.message != CANEAL_TOCKEN_STATUS) {
              this.$toast({
                message: error.message,
              });
            }
            return;
          }

          // 第一次查询队列成功/失败：不需要再轮询了
          if (switchSuss) {
            return;
          }
          this.waitSwtichStaff(true);

          break;
        case WAIT_STATUS_ENUM.success:
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.hangUpCard,
            content: "",
            customId: getUId(),
            kfId: this.chatInfo.staff_info.user_id,
            kfname: this.chatInfo.staff_info.nickname,
            session_id: resp.session_id,
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.staff,
            contentType: CHAT_CONTENT_TYPE_ENUM.text,
            content: {
              text: `您好，客服${this.chatInfo.staff_info.nickname}为您服务，请问您有什么需要咨询的问题吗？`,
            },
            customId: getUId(),
            sendSuss: true,
            sendLoading: false,
            showMoreBtn: false,
            moreId: getUId(),
            kfId: this.chatInfo.staff_info.user_id,
            kfname: this.chatInfo.staff_info.nickname,
            kfAvatar: this.chatInfo.staff_info.avatar_url,
          });
          this.waitInfo.state = WAIT_STATUS_ENUM.success;
          this.waitInfo.session_id = resp.session_id;
          this.chatInfo.session_id = resp.session_id;
          this.watchChatActive();
          break;
        case WAIT_STATUS_ENUM.transfer:
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.roundTip,
            customId: getUId(),
            content: "客服转接中，请稍后…",
          });
          this.goEnd(null, 100);
          // 转接其他客服
          this.switchStaff();
          break;
        case WAIT_STATUS_ENUM.fail:
          // 留言框
          this.chatList.push({
            sendSuss: true,
            sendLoading: false,
            customId: getUId(),
            role: CHAT_ROLE_TYPE_ENUM.leaveMsg,
          });
          break;
        case WAIT_STATUS_ENUM.evaluate:
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "- 本次服务已完成，请为本次服务做出评价! -",
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.evaluate,
            customId: getUId(),
            content: 0,
            session_id: this.chatInfo.session_id,
          });
          break;
        default:
          break;
      }
    },
    async initMessageInfo() {
      const req = {
        channel: this.channelId, // 通过h5链接解析出来的渠道参数
      };
      let resp;
      try {
        resp = await messageInit(req);
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }
      resp.chat_window.name = resp.chat_window.name || DEFAULT_TITLE;
      resp.chat_window.prompt = resp.chat_window.prompt || DEFAULT_PROMPT;

      this.chatInfo = resp;
      document.title = this.chatInfo.chat_window.name;
      return resp;
    },
    // 初始化轮播图
    async initBanner() {
      console.log("initBanner invoke");

      const req = {
        session_id: this.chatInfo.session_id,
      };
      let resp;
      try {
        resp = await getBannerOne(req);
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }

      this.bannerInfo = resp;
      if (!this.bannerInfo.image_url) {
        document.getElementsByClassName("home")[0].style["padding-top"] = 0;
      }
    },
    // 初始化热门问题
    async initHotIssueList() {
      console.log("initHotIssueList invoke");

      let resp;
      try {
        resp = await getHotIssueList();
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }

      resp.issue_list = resp.issue_list.map((i) => {
        return {
          ...i,
          selfId: i.id,
        };
      });
      console.log("=======getHotIssueList", resp);

      this.chatList.unshift({
        role: CHAT_ROLE_TYPE_ENUM.hotIssues,
        content: resp,
      });

      this.hotIssueInfo = resp;
    },
    // 初始化乘车记录
    async initRideList(params) {
      console.log("initRideList invoke", params);

      const req = {
        session_id: this.chatInfo.session_id,
        page: params.page,
        size: params.size,
        pay_method: params.pay_method,
      };
      let resp;
      try {
        resp = await getRideList(req);
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
          error.status != NETWORK_ERROR_STATUS
        ) {
          throw new Error(error.msg);
        } else {
          throw new Error(error.status);
        }
      }

      resp.riding_list = resp.riding_list.map((i) => {
        return {
          ...i,
          selfId: i.id,
        };
      });

      const rideItem = this.rideTabList.tabList.find((i) => {
        return i.value == params.pay_method;
      });

      if (!resp.riding_list.length) {
        rideItem.finished = true;
        rideItem.loading = false;
        return;
      }
      if (params.refresh) {
        rideItem.list = resp.riding_list || [];
      } else {
        rideItem.list.push(...(resp.riding_list || []));
      }

      rideItem.loading = false;
      rideItem.page++;
    },
    // 初始化查看更多
    initMoreBtn() {
      this.$nextTick(() => {
        this.chatList = this.chatList.map((i) => {
          if (i.contentType == CHAT_CONTENT_TYPE_ENUM.auto) {
            const a = document.getElementById(i.moreId);
            if (a.scrollHeight > a.clientHeight + 10) {
              console.log("机器人回复：查看更多=>", i);
              i.showMoreBtn = true;
            } else {
              i.showMoreBtn = false;
            }
          }
          return i;
        });
      });
    },
    //------------------------------------------------------------------
    watchChatActive() {
      clearInterval(this.messageQuitTimer);
      this.messageQuitTimer = null;
      this.messageQuitTime = 0;
      this.messageQuitTipEnd = false;

      // 用户回复超出时长
      this.messageQuitTimer = setInterval(() => {
        this.messageQuitTime += 1;
        if (!this.messageQuitTipEnd && this.messageQuitTime >= 180) {
          console.log("180秒不发消息，超时提醒");
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "会话回复已超时，请及时回复哦，我们将全力协助您解决",
          });
          this.goEnd(null, 100);
          this.messageQuitTipEnd = true;
        }
        if (this.messageQuitTime >= 220) {
          console.log("220秒不发消息，退出会话");
          clearInterval(this.messageQuitTimer);
          this.messageQuitTimer = null;
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content:
              "会话回复超时，会话挂断，本次服务已完成，请为本次服务做出评价！",
          });
          this.goEnd(null, 100);
          this.handleChatClose(true, this.waitInfo.session_id);
        }
      }, 1000);
    },
    // 查询客服排队
    async waitSwtichStaff(abnormal = false) {
      let timer = setInterval(async () => {
        try {
          const resp = await this.getQueueStatus();
          console.log("==========getQueueStatus", resp);

          if (
            resp.state == WAIT_STATUS_ENUM.success ||
            resp.in_queue_count == 0
          ) {
            // 接入成功
            clearInterval(timer);
            timer = null;
            // 转接类型
            if (abnormal === true) {
              this.chatList.push({
                role: CHAT_ROLE_TYPE_ENUM.defaultTip,
                customId: getUId(),
                content: "- 客服接入成功，很高兴为您服务！-",
              });
            } else {
              // 正常接入类型
              this.chatList.push({
                role: CHAT_ROLE_TYPE_ENUM.hangUpCard,
                content: "",
                customId: getUId(),
                kfId: resp.staff_info.user_id,
                kfname: resp.staff_info.nickname,
                session_id: resp.session_id,
              });
            }

            this.chatList.push({
              role: CHAT_ROLE_TYPE_ENUM.staff,
              contentType: CHAT_CONTENT_TYPE_ENUM.text,
              content: {
                text: `您好，客服${resp.staff_info.nickname}为您服务，请问您有什么需要咨询的问题吗？`,
              },
              customId: getUId(),
              sendSuss: true,
              sendLoading: false,
              showMoreBtn: false,
              moreId: getUId(),
              kfId: resp.staff_info.user_id,
              kfname: resp.staff_info.nickname,
              kfAvatar: resp.staff_info.avatar_url,
            });
            this.watchChatActive();
            this.chatInfo.session_id = resp.session_id;
          } else if (resp.state == WAIT_STATUS_ENUM.inqueue) {
            // 排队中
            if (this.waitInfo.in_queue_count === resp.in_queue_count) {
              //排队人数不变，不重新发送
              return;
            }
            this.chatList.push({
              role: CHAT_ROLE_TYPE_ENUM.roundTip,
              customId: getUId(),
              content: `在当前排队${resp.in_queue_count}人，请稍后…`,
            });
          } else if (resp.state == WAIT_STATUS_ENUM.fail) {
            // 转留言
            clearInterval(timer);
            timer = null;
            this.chatList.push({
              role: CHAT_ROLE_TYPE_ENUM.leaveMsgBtn,
              customId: getUId(),
              content:
                "很抱歉，暂时无法为您提供服务，请留言我们会尽快与您取得联系！",
            });
          }
          this.goEnd(null, 100);
          this.waitInfo = resp;
        } catch (error) {
          if (error.message == NETWORK_ERROR_STATUS) {
            this.chatList.push({
              role: CHAT_ROLE_TYPE_ENUM.defaultTip,
              customId: getUId(),
              content: "- 网络异常 请稍后重试 -",
            });
            this.goEnd(null, 100);
          } else if (error.message != CANEAL_TOCKEN_STATUS) {
            this.$toast({
              message: error.message,
            });
          }
          clearInterval(timer);
          timer = null;
          return;
        }
      }, 2000);
    },
    // 消息数据处理
    filterMessage(messageList) {
      return messageList.map((i) => {
        const chat = {
          selfId: i.selfId,
          message_id: i.message_id,
          role: "",
          contentType: i.message_type,
          content: i.content,
          createUnix: i.create_unix,
          sendSuss: true,
          sendLoading: false,
          showMoreBtn: false,
          id: getUId(),
          moreId: getUId(),
          customId: getUId(),
        };
        if (i.belong_to == BELONE_TO_ENUM.user) {
          // 用户消息
          chat.role = CHAT_ROLE_TYPE_ENUM.user;
        } else if (
          i.belong_to == BELONE_TO_ENUM.staff &&
          i.staff_info.user_id
        ) {
          // 客服消息
          chat.role = CHAT_ROLE_TYPE_ENUM.staff;
          chat.kfId = i.staff_info.user_id;
          chat.kfname = i.staff_info.nickname;
          chat.kfAvatar = i.staff_info.avatar_url;
        } else {
          // 机器人消息
          chat.role = CHAT_ROLE_TYPE_ENUM.robot;
          chat.contentType = CHAT_CONTENT_TYPE_ENUM.auto;

          chat.kfname = this.chatInfo.chat_window.robot.nickname;
          chat.kfAvatar = this.chatInfo.chat_window.robot.avatar_url;
        }
        return chat;
      });
    },
    // 关联问题
    async getRelateIssueList(message) {
      const req = {
        session_id: this.chatInfo.session_id,
        message: message,
      };
      let resp;
      try {
        resp = await getRelateIssueList(req);
      } catch (error) {
        this.$toast({
          message: error.msg,
        });
        return [];
      } finally {
        this.relateIssueLoading = false;
      }

      resp.issue_list = resp.issue_list || [];

      resp.issue_list = resp.issue_list.map((i) => {
        return {
          ...i,
          selfId: i.id,
        };
      });

      console.log("==========getRelateIssueList", resp);
      if (resp.issue_list.length) {
        this.chatList.push({
          customId: getUId(),
          role: CHAT_ROLE_TYPE_ENUM.relatedIssues,
          content: resp.issue_list,
        });
        // 预留：可以先不可见，滚动到底部后可见
        this.goEnd();
      }
    },

    // 轮询查消息
    async getMessageRegularTime() {
      // 重新计时
      clearInterval(this.kfReply.timer);
      this.kfReply.timer = null;
      this.kfReply.time = 180;

      this.kfReply.timer = setInterval(async () => {
        // 预留，测试联调的时候关注
        this.kfReply.time--;
        if (this.kfReply.time <= 0) {
          console.log(
            "======：客服回复超出时长超时，转接其他客服--->留言大弹窗"
          );
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content:
              "很抱歉，目前无法准确定位您的问题，我们正在为您转接其他客服",
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.roundTip,
            customId: getUId(),
            content: "客服转接中，我们正在为您转接其他客服，请稍后…",
          });
          this.goEnd(null, 100);
          clearInterval(this.kfReply.timer);
          this.kfReply.timer = null;
          // 转接其他客服
          this.switchStaff();
          return;
        }
      }, 1000);

      if (this.kfReply.time <= 0) {
        return;
      }
      const startItem = deepClone(this.chatList)
        .reverse()
        .find((i) => i.selfId);
      let messageList = await this.getMessage({
        mode: PULL_MESSAGE_TYPE_ENUM.next,
        start_message_id: startItem ? startItem.message_id : "",
      });
      messageList = this.filterMessage(messageList);

      if (this.kfReply.time <= 0) {
        return;
      }
      console.log("=======getMessageRegularTime", messageList);

      const tempList = [];
      messageList.forEach((j) => {
        // 用户刚刚发送的消息
        const chatList = this.chatList.filter(
          (i) => !i.selfId && i.role == CHAT_ROLE_TYPE_ENUM.user
        );

        const text = chatList.find(
          (i) =>
            i.contentType == CHAT_CONTENT_TYPE_ENUM.text &&
            j.contentType == CHAT_CONTENT_TYPE_ENUM.text &&
            i.content.text == j.content.text
        );
        const image = chatList.find(
          (i) =>
            i.contentType == CHAT_CONTENT_TYPE_ENUM.image &&
            j.contentType == CHAT_CONTENT_TYPE_ENUM.image &&
            i.content.image == j.content.image
        );
        const ride = chatList.find(
          (i) =>
            i.contentType == CHAT_CONTENT_TYPE_ENUM.ride &&
            j.contentType == CHAT_CONTENT_TYPE_ENUM.ride &&
            i.content.ride_record.id == j.content.ride_record.id
        );
        if (text || image || ride) {
          // 替换，不严谨就直接return，不替换了
          const replaceIndex = this.chatList.findIndex(
            (i) => i.customId == (text || image || ride).customId
          );
          if (replaceIndex > -1) {
            this.chatList[replaceIndex] = { ...(text || image || ride), ...j };
          }
        } else {
          // 直接push
          tempList.push(j);
        }
      });

      this.chatList.push(...tempList);
      this.addChatTime(this.chatList);
      this.initMoreBtn();
      this.goEnd();

      this.getMessageRegularTime();
    },
    // 重新发送
    async handleReSendMeaasge(item) {
      console.log("=======handleReSendMeaasge", item);

      item.sendLoading = true;

      if (item.contentType == CHAT_CONTENT_TYPE_ENUM.text) {
        const req = {
          message: item.content,
          type: CHAT_CONTENT_TYPE_ENUM.text,
        };
        try {
          await this.createMessage(req);
        } catch (error) {
          item.sendSuss = false;
          return;
        } finally {
          item.sendLoading = false;
        }
        item.sendSuss = true;

        // 判断是否正在请求关联问题中
        if (this.relateIssueLoading) {
          return;
        }
        this.relateIssueLoading = true;

        await timeout(1000);
        this.getRelateIssueList(item.content);
      }
      if (item.contentType == CHAT_CONTENT_TYPE_ENUM.image) {
        let uploadResp;
        let file = blobToFile({
          blob: item.file.blob,
          filename: item.file.filename,
          type: item.file.type,
        });
        // 测试
        let a = await fileToBase64(file);
        console.log("==========", item.file, file, a);

        try {
          uploadResp = await uploadFile({
            session_id: this.chatInfo.session_id,
            fileData: file,
          });
        } catch (error) {
          item.sendSuss = false;
          item.sendLoading = false;

          return;
        }

        const req = {
          message: uploadResp.download_url,
          type: CHAT_CONTENT_TYPE_ENUM.image,
        };
        try {
          await this.createMessage(req);
        } catch (error) {
          item.sendSuss = false;
          return;
        } finally {
          item.sendLoading = false;
        }
        item.sendSuss = true;
      }
      if (item.contentType == CHAT_CONTENT_TYPE_ENUM.ride) {
        const req = {
          message: item.selfId,
          type: CHAT_CONTENT_TYPE_ENUM.ride,
        };

        try {
          await this.createMessage(req);
        } catch (error) {
          item.sendSuss = false;
          return;
        } finally {
          item.sendLoading = false;
        }

        item.sendSuss = true;
      }
      // 1、find找到，删除，2、拉取到后push
      const deleteIndex = this.chatList.findIndex(
        (i) => i.customId == item.customId
      );
      if (deleteIndex > -1) {
        this.chatList.splice(deleteIndex, 1);
      }
    },
    // 转接客服
    async switchStaff() {
      let switchSuss = false;
      try {
        const resp = await this.msqueueJoin();
        console.log("==========msqueueJoin", resp);

        if (resp.state == WAIT_STATUS_ENUM.success) {
          // 接入成功
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "- 客服接入成功，很高兴为您服务！-",
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.staff,
            contentType: CHAT_CONTENT_TYPE_ENUM.text,
            content: {
              text: `您好，客服${resp.staff_info.nickname}为您服务，请问您有什么需要咨询的问题吗？`,
            },
            customId: getUId(),
            sendSuss: true,
            sendLoading: false,
            showMoreBtn: false,
            moreId: getUId(),
            kfId: resp.staff_info.user_id,
            kfname: resp.staff_info.nickname,
            kfAvatar: resp.staff_info.avatar_url,
          });
          switchSuss = true;
          this.chatInfo.session_id = resp.session_id;
          this.watchChatActive();
        } else if (resp.state == WAIT_STATUS_ENUM.inqueue) {
          // 排队中
          if (this.waitInfo.in_queue_count === resp.in_queue_count) {
            //排队人数不变，不重新发送
            return;
          }
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "转接客服失败，当前无空闲人员接待，已为您进入排队队列",
          });
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.roundTip,
            customId: getUId(),
            content: `在当前排队${resp.in_queue_count}人，请稍后…`,
          });
        } else if (resp.state == WAIT_STATUS_ENUM.fail) {
          // 转留言
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.leaveMsgBtn,
            customId: getUId(),
            content:
              "转接客服失败，暂时无法为您提供服务，请留言我们会尽快与您取得联系！",
          });
          switchSuss = true;
        }
        this.goEnd(null, 100);
        this.waitInfo = resp;
        // 第一次查询队列成功/失败：不需要再轮询了
        if (switchSuss) {
          return;
        }
        // 定时查询
        this.waitSwtichStaff(true);
      } catch (error) {
        if (error.message == NETWORK_ERROR_STATUS) {
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "- 网络异常 请稍后重试 -",
          });
          this.goEnd(null, 100);
        } else if (error.message != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.message,
          });
        }
        return;
      }
    },
