    async getQueueStatus() {
      const req = {
        session_id: this.chatInfo.session_id,
      };
      let resp;
      try {
        resp = await getMsqueueStatus(req);
      } catch (error) {
        if (
          error.status != CANEAL_TOCKEN_STATUS &&
