    // 挂断聊天
    async handleChatClose(noReplay = false, sessionId) {
      console.log("挂断聊天", this.waitInfo.session_id, sessionId);

      // 挂断后不能再挂断
      if (!this.waitInfo.session_id) {
        return;
      }
      // 通过session_id判断挂断是否是当前客服
      if (this.waitInfo.session_id != sessionId) {
        return;
      }

      const req = {
        session_id: sessionId,
      };
      console.log("挂断聊天", req);
      try {
        await msqueueClose(req);
      } catch (error) {
        if (error.status == NETWORK_ERROR_STATUS) {
          this.chatList.push({
            role: CHAT_ROLE_TYPE_ENUM.defaultTip,
            customId: getUId(),
            content: "- 网络异常 请稍后重试 -",
          });
          this.goEnd(null, 100);
        } else if (error.status != CANEAL_TOCKEN_STATUS) {
          this.$toast({
            message: error.msg,
          });
        }

        return;
      }
      clearInterval(this.messageQuitTimer);
      this.messageQuitTimer = null;

      if (noReplay !== true) {
        this.chatList.push({
          role: CHAT_ROLE_TYPE_ENUM.defaultTip,
          customId: getUId(),
          content: "- 本次服务已完成，请为本次服务做出评价! -",
        });
      }

      this.chatList.push({
        role: CHAT_ROLE_TYPE_ENUM.evaluate,
        customId: getUId(),
        content: 0,
        session_id: this.waitInfo.session_id,
      });
      this.initMessageInfo();
      this.waitList.push(this.waitInfo);
      this.waitInfo = {};
      this.goEnd(null, 100);
    },
