    // 下拉加载更多
    async handleLoadHistoryList() {
      const startItem = deepClone(this.chatList).find((i) => i.selfId);
      let messageList = await this.getMessage({
        mode: PULL_MESSAGE_TYPE_ENUM.prev,
        start_message_id: startItem.message_id,
      });

      messageList = this.filterMessage(messageList);
      if (!messageList.length) {
        this.chatFinished = true;
        this.chatRefreshing = false;
        this.chatList.unshift({
          role: CHAT_ROLE_TYPE_ENUM.marginTip,
          customId: getUId(),
          content: "暂无更多历史记录~",
        });
        return;
      }
      this.chatList.unshift(...messageList);
      this.addChatTime(this.chatList, true);

      const loadingHeight = document.getElementsByClassName(
        "van-loading van-loading--circular"
      )[0].offsetHeight;
      this.chatRefreshing = false;

      const dom = document.querySelector(".chat-container");
      this.initMoreBtn();
      this.$nextTick(() => {
        const firstMessage = document.getElementById(this.firstMsgId);

        firstMessage.setAttribute("tabindex", "1");
        firstMessage.style.outline = "none";
        dom.scrollTop =
          document.getElementById(`${this.firstMsgId}`).offsetTop -
          loadingHeight;

        this.setFirstMsgId();
      });
    },
    // 插入时间
    addChatTime(arr, loadHistory) {
      let createUnix;

      if (!arr.length) {
        return;
      }
      if (loadHistory) {
        let startIndex = JSON.parse(JSON.stringify(arr)).findIndex(
          (i) => i.tips
        );

        for (let index = startIndex; index >= 0; index--) {
          const i = arr[index];

          if (index === startIndex) {
            createUnix = i.createUnix;
          } else {
            if (i.createUnix && i.createUnix + 5 * 60 <= createUnix) {
              console.log("超过5分钟=>", timestamp2String(i.createUnix), arr);

              let content;
              if (
                timestamp2String(i.createUnix, "YYYY-MM-DD") ==
                timestamp2String(createUnix, "YYYY-MM-DD")
              ) {
                content = timestamp2String(i.createUnix, "HH:mm");
              } else {
                content = timestamp2String(i.createUnix, "YYYY-MM-DD HH:mm");
              }

              arr.splice(index, 0, {
                createUnix: i.createUnix,
                tips: timestamp2String(i.createUnix),
                role: CHAT_ROLE_TYPE_ENUM.defaultTip,
                content: content,
              });
              // 作为后续的第一个判定
              createUnix = i.createUnix;
            }
          }
        }

        return;
      } else {
        let startIndex = JSON.parse(JSON.stringify(arr))
          .reverse()
          .findIndex((i) => i.tips);
        if (startIndex < 0) {
          startIndex = 0;
        } else {
          startIndex = arr.length - 1 - startIndex;
        }

        for (let index = startIndex; index < arr.length; index++) {
          const i = arr[index];

          if (index === startIndex) {
            arr.splice(index, 0, {
              createUnix: i.createUnix,
              tips: timestamp2String(i.createUnix),
              role: CHAT_ROLE_TYPE_ENUM.defaultTip,
              customId: getUId(),
              content: timestamp2String(i.createUnix, "YYYY-MM-DD HH:mm"),
            });
            createUnix = i.createUnix;
          } else {
            if (i.createUnix >= createUnix + 5 * 60) {
              console.log(
                "超过5分钟=>",
                i.createUnix,
                createUnix + 5 * 60,
                timestamp2String(i.createUnix),
                timestamp2String(createUnix + 5 * 60)
              );

              let content;
              if (
                timestamp2String(i.createUnix, "YYYY-MM-DD") ==
                timestamp2String(createUnix, "YYYY-MM-DD")
              ) {
                content = timestamp2String(i.createUnix, "HH:mm");
              } else {
                content = timestamp2String(i.createUnix, "YYYY-MM-DD HH:mm");
              }

              arr.splice(index, 0, {
                createUnix: i.createUnix,
                tips: timestamp2String(i.createUnix),
                role: CHAT_ROLE_TYPE_ENUM.defaultTip,
                customId: getUId(),
                content: timestamp2String(i.createUnix, "YYYY-MM-DD HH:mm"),
              });
              // 作为后续的第一个判定
              createUnix = content;
            }
          }
        }
      }
    },
